import React, { Component } from 'react';
import { Button, Modal, Icon } from 'semantic-ui-react';
import { Document, Page, Text, View, StyleSheet, PDFViewer } from "@react-pdf/renderer";


// Create styles
  const styles = StyleSheet.create({
    page: {
      backgroundColor: "white",
      color: "black",
    },
    section: {
      margin: 10,
      padding: 10,
    },
    viewer: {
      width: window.innerWidth / 2 , //the pdf viewer will take up all of the width and height
      height: window.innerHeight / 2,
    },
  });
  
  // Create Document Component
class ShowPDF extends Component { 
    
    render()
    {  
    return(
        <Modal trigger={
      
            <Button color='red' icon size='mini'>
              <Icon color='red' name='world' />
                Comprobante
            </Button> }
            
            dimmer='inverted'
              size='small'
              closeIcon
            >

      <PDFViewer style={styles.viewer}>
        {/* Start of the document*/}
        <Document>
          {/*render a single page*/}
          <Page size="A4" style={styles.page}>
            <View style={styles.section}>
              <Text>Comprobante: Cementerio del GAD de Sayausi</Text>
            </View>
            <View style={styles.section}>
                <Text>Boveda: {this.props.valores.boveda}</Text>
                <Text>Nombre: {this.props.valores.nombre}</Text>
            </View>
          </Page>
        </Document>
      </PDFViewer>

      </Modal> 
    );
  }
}

export default ShowPDF;